<template>
  <div class="hello">
    <form @submit.prevent="addTask">
      <el-input type="text" placeholder="Veuillez entrer una tâche à effectuer" v-model="task" name="task"></el-input>
    </form>
    <ul>
      <transition-group name="list" enter-active-class="animated bounceInUp">
        <li v-for="(data, index) in tasks" :key="index">
          <i class="el-icon-check"></i>
          {{data.task}}
          <i class="el-icon-delete right" @click="removeTask(index)"></i>
        </li>
      </transition-group>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Todo',
  data () {
    return {
      task: '',
      tasks: [
      ]
    }
  },
  methods: {
    addTask () {
      this.tasks.push({task: this.task})
      this.task = ''
    },
    removeTask (id) {
      this.tasks.splice(id, 1)
    }
  }
}
</script>

